import uniPopup from '../../components/uni-popup/uni-popup.vue'

export default {
    onLoad(e) {
        //判断是否快捷维修入口
        let sid = e.id
        console.log(e)
        console.log(e.id)
        if(sid == ''){
            return
        }else{
            this.$refs.popup.open()
            let xtypeId = this.faulesList[sid].type
            uni.request({
                url:'https://www.finetwm.com/xmRepair/faults/getDetail',
                method:'POST',
                data:{
                    type:xtypeId
                },
                success: res=>{
                    console.log(res.data.data)
                    this.faulesItem = res.data.data
                }
            })
        }
    },
    data() {
        return {
            ftype:'',
            faulesItemPrice:[],
            currentPrice:0,
            totalPrice: 0,
            isSelectedId: '',
            isSelected: false,
            unSelectedImg: '../../static/faults/wxz@2x.png',
            selectedImg: '../../static/faults/wxz@2x.png',
            faulesItem: [],
            faulesList: [{
                imgSrc: '../../static/faults/screen@2x.png',
                text: '屏幕问题',
                type: "screen"
            }, {
                imgSrc: '../../static/faults/battery@2x.png',
                text: '电池电源问题',
                type: "battery"
            }, {
                imgSrc: '../../static/faults/signal@2x.png',
                text: '连接/信号问题',
                type: "signal"
            }, {
                imgSrc: '../../static/faults/camare@2x.png',
                text: '摄像头问题',
                type: "camera"
            }, {
                imgSrc: '../../static/faults/voice@2x.png',
                text: '声音问题',
                type: "voice"
            }, {
                imgSrc: '../../static/faults/botton@2x.png',
                text: '按键问题',
                type: "keyboard"
            }, {
                imgSrc: '../../static/faults/shield@2x.png',
                text: '外壳边框问题',
                type: "edging"
            }, {
                imgSrc: '../../static/faults/board@2x.png',
                text: '主板维修',
                type: "mainboard"
            }, {
                imgSrc: '../../static/faults/switchoff@2x.png',
                text: '开关机问题',
                type: "switch"
            }, {
                imgSrc: '../../static/faults/unkeep@2x.png',
                text: '保养服务',
                type: "maintain"
            }, {
                imgSrc: '../../static/faults/install@2x.png',
                text: '安装服务',
                type: "install"
            }, {
                imgSrc: '../../static/faults/other@2x.png',
                text: '其他故障',
                type: "others"
            }]
        }
    },
    methods: {
        onSelected(e) {
            this.isSelectedId = e.currentTarget.id
        },
        openPopup(sid) {
            this.$refs.popup.open()
            let xtypeId = this.faulesList[sid].type
            uni.request({
                url:'https://www.finetwm.com/xmRepair/faults/getDetail',
                method:'POST',
                data:{
                    type:xtypeId
                },
                success: res=>{
                    // console.log(res.data.data)
                    this.faulesItem = res.data.data
                }
            })
        },
        closePopup() {
            this.$refs.popup.close()
        },
        onsiteRepair(e) {
            console.log(e)
            uni.navigateTo({
                url: '../onsiterepair/onsiterepair'
            })
        },
        onsiteArrival(){
            uni.navigateTo({
                url:'../arrival-repair/arrival-repair'
            })
        },
        // 多选框判断选取项
        onEventChange(e){
            let eventChange = e.detail.value
            console.log(e.detail.value)
            let s = 0   
            for (var i = 0; i < eventChange.length; i++) {
                let currentPrice = parseInt(this.faulesItem[eventChange[i]].price)
                s = s+ currentPrice
                console.log(s)
                this.totalPrice = s
                uni.setStorage({
                    key:'totalPrice',
                    data:s
                })
            }
        }
    },
    components: {
        uniPopup
    }
}
